import { createRouter, createWebHistory } from 'vue-router'


const routes = [
  {
    path:'/',
    redirect:'/login'
  },
  {
    path:'/login',
    component:() => import('../views/Login')
  },
  {
    path:'/adminLayout',
    component:() => import('../components/admin/AdminLayout'),
    children:[
      {
        path:'',
        component:() => import('../views/Welcome')
      },
      {
        path:'car',
        component:() => import('../views/admin/Car')
      },
      {
        path:'customer',
        component:() => import('../views/admin/Customer')
      },
      {
        path:'driver',
        component:() => import('../views/admin/Driver')
      },
      {
        path:'auditOrder',
        component:() => import('../views/admin/AuditOrder')
      },
      {
        path:'allOrder',
        component:() => import('../views/admin/AllOrder')
      }
  ]
},
  {
    path:'/customerLayout',
    component:() => import('../components/customer/CustomerLayout'),
    children:[
      {
        path:'',
        component:() => import('../views/Welcome')
      },
      {
        path:'customerBuyOrder',
        component:() => import('../views/customer/CustomerBuyOrder')
      },
      {
        path:'customerFindOrder',
        component:() => import('../views/customer/CustomerFindOrder')
      }

    ]
  },
  {
    path:'/driverLayout',
    component:() => import('../components/driver/DriverLayout'),
    children:[
      {
        path:'',
        component:() => import('../views/Welcome')
      },
      {
        path:'driverOrderTable',
        component:() => import('../views/driver/DriverOrderTable')
      }
    ]
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
